import * as React from 'react';
import './Head.css';
import ReturnProfil from './Profil';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import Divider from '@mui/material/Divider';

import { collection, getDocs, doc, onSnapshot } from "firebase/firestore";
import { db } from '../../../../firebase';

import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';


let Acces = true;
let listRising = new Array();

function DrawerAppBar() {

 const [load, setLoad] = React.useState(false);

 const pushDocs = JSON.parse(window.localStorage.getItem('&&view$$listton'));
 const pushOther = JSON.parse(window.localStorage.getItem('&&view$$listtoncol-'));

 React.useEffect(async () => {
  [...pushOther].map((item) => {

   const unsub = onSnapshot(doc(db, "tontine", item), (doc) => {
    window.setTimeout(() => {
     listRising.push(doc.data().rising);
    }, 500);

   });

  })
 }, []);

 if (Array.isArray(pushDocs) && pushDocs.length) {
  Acces = true;
 } else {
  Acces = false;
 };


 return (
  <>
   <div className='zindex-theme'>
    <Backdrop
     sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
     open={load}>

     <CircularProgress color="inherit" />
    </Backdrop>
   </div>

   <div className='flex-head-list-tontine'>
    <header>
     <div className='container'>
      <nav className='navbar'>
       <ReturnProfil />

      </nav>
     </div>
    </header>

    <section>
     {
      Acces ?
       <ul>
        {
         [...Array(pushDocs.length).keys()].map(index => {
          return (
           <div onClick={async () => {

            setLoad(true);
            window.localStorage.setItem('list22fil', JSON.stringify(pushOther[index]));
            const unsub = onSnapshot(doc(db, "tontine", pushOther[index]), (doc) => {
             window.localStorage.setItem('list', JSON.stringify((doc.data().table)));
            });

            window.setTimeout(() => {
             window.location.href = "/tontine/list/group/child";
            }, 5800);

           }}>

            <List>
             <ListItem disablePadding>
              <ListItemButton>

               <li key={index}>
                <div className='cmd-operator-title'>
                 <div className='cmd-operator-sub-title'>

                  <div className='flex-row-cmd'>
                   <p>{pushDocs[index].charAt(0).toUpperCase() + pushDocs[index].slice(1)}</p>
                  </div>
                 </div>

                </div>

               </li>

              </ListItemButton>
             </ListItem>
            </List>
            <Divider />
           </div>
          )
         })}
       </ul>
       : <div></div>
     }
    </section>
   </div>
  </>
 );
}

export default DrawerAppBar;